#quote-box {
  /* position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%); */
  border-radius: 10px;
  padding: 30px;
  color: rgb(238, 184, 84);
  box-shadow: 10px 10px 20px rgba(10, 10, 10, 0.411);
  background: transparent;
}
#text {
  font-size: 30px;
  font-weight: bold;
  text-align: center;
}
#text::before {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  content: "â€œ";
  font-size: 250px;
  position: relative;
  top: 125px;
  color: #0e0e0e62;
  line-height: 0;
}
#author {
  font-size: 20px;
  text-align: right;
  font-weight: 500;
}
#new-quote {
  border: none;
  background-color: rgb(248, 211, 143);
  border-radius: 10px;
  padding: 10px;
  margin-left: 50px;
  transition: 0.5s ease;
  outline: none !important;
  transition: all 200ms ease;
  user-select: none;
}
#new-quote:hover {
  background-color: rgb(228, 212, 183);
}
#new-quote:active {
  background-color: rgb(245, 224, 186);
  transform: scale(0.9);
}
.twitter,
#tweet-quote {
  background-color: transparent;
  color: rgb(248, 211, 143);
  border: none;
  font-size: 20px;
  transition: 0.2s ease;
}
.twitter:hover {
  transform: translateY(-3px);
}
.bg{
  width: 100%;
  height: 110vh;
  background-size: 400% 400%;
  background-image: linear-gradient(45deg, #a3ff4e, #4f5bff, rgb(113, 0, 136), #4f5bff, #a3ff4e);
  background-repeat: no-repeat;
  animation: animate 10s infinite normal;
  display: flex;
  justify-content: center;
  align-items: center;
}
@keyframes animate {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}
#loading {
  font-size: 50px;
  text-align: center;
  color: rgb(245, 207, 137);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
#loading::after {
  content: "";
  animation: load 3s infinite;
  transition: 0.2s ease;
}
@keyframes load {
  0% {
    content: "";
  }
  25% {
    content: ".";
  }
  50% {
    content: "..";
  }
  75% {
    content: "...";
  }
  100% {
    content: "";
  }
}
@media only screen and (max-width: 650px) {
  #quote-box {
    width: 80%;
    margin-top: 20%;
  }
  .bg {
    height: 120vh;
  }
  #text {
    font-size: 20px;
  }
  #text::before {
    font-size: 90px;
  }
}
